import { Component, OnInit } from '@angular/core';
import { SubadminService } from '../../services/subadmin.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-editsubadmin',
  templateUrl: './editsubadmin.component.html',
  styleUrls: ['./editsubadmin.component.scss']
})
export class EditsubadminComponent implements OnInit {

  userForm: any;
  driverid;
 
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private service: SubadminService,
    private _router: Router,
    
  ) {

    this.route.params.subscribe((params) => {
      this.driverid = params['driverid'];
      console.log(this.driverid)
      });

    this.userForm = this.formBuilder.group({
      'name': ['', Validators.required],
      'email': ['', Validators.required],
      'phoneNumber': ['', Validators.required],
      'countryCode': ['', Validators.required]
    });
  }

  ngOnInit() {
  }

}
