import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SubadmininfoComponent } from './subadmininfo/subadmininfo.component';

import { Routes, RouterModule } from '@angular/router';
import { LayoutModule } from '../theme/layouts/layout.module';
import { DefaultComponent } from '../theme/pages/default/default.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PagerService } from '../services/pager.service';
import { AddsubadminComponent } from './addsubadmin/addsubadmin.component';
import { EditsubadminComponent } from './editsubadmin/editsubadmin.component';
import { ViewsubadminComponent } from './viewsubadmin/viewsubadmin.component';

const routes: Routes = [
    {
        'path': '',
        'component': DefaultComponent,
        'children': [
            {
                'path': '',
                'component': SubadmininfoComponent,
            },
            {
                'path': 'edit-subadmin/:driverid',
                'component': EditsubadminComponent,
              },
              {
                'path': 'add-subadmin',
                'component': AddsubadminComponent,
              },
              {
                'path': 'view-subadmin/:driverid',
                'component': ViewsubadminComponent,
              }
        ],
    },
];


@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        LayoutModule,
        FormsModule,
        ReactiveFormsModule,
    ],
    declarations: [SubadmininfoComponent, AddsubadminComponent, EditsubadminComponent, ViewsubadminComponent],
    providers: [
        PagerService
    ]
})

export class SubadminModule { }
