import { Component, OnInit } from '@angular/core';
import { SubadminService } from '../../services/subadmin.service';
import { PagerService } from '../../services/pager.service'

@Component({
  selector: 'app-subadmininfo',
  templateUrl: './subadmininfo.component.html',
  styleUrls: ['./subadmininfo.component.scss']
})
export class SubadmininfoComponent implements OnInit {

  closeResult: string;
  Users: any[];
  name: string;
  modalRef: any;
  driverRef: any = null;
 
  // pager object
  pager: any = {};
  // paged items
  pagedItems: any[];

  constructor(
    private service: SubadminService,
    private pagerService: PagerService
  ) { }

  ngOnInit() {
    this.getUsers();
  }

  getUsers() {
    this.service.getUsers().subscribe(res => {
      this.Users = res.data.users;
      this.setPage(1);
    });
  }

  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagerService.getPager(this.Users.length, page);

    // get current page of items
    this.pagedItems = this.Users.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }
}
