import { Component, OnInit } from '@angular/core';
import { SubadminService } from '../../services/subadmin.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-addsubadmin',
  templateUrl: './addsubadmin.component.html',
  styleUrls: ['./addsubadmin.component.scss']
})
export class AddsubadminComponent implements OnInit {

  userForm: any;
  driverid;
 
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private service: SubadminService,
    private _router: Router,
    
  ) {

    this.route.params.subscribe((params) => {
      this.driverid = params['driverid'];
      console.log(this.driverid)
      });


    this.userForm = this.formBuilder.group({
      'name': ['', Validators.required],
      'email': ['', Validators.required],
      'phoneNumber': ['', Validators.required],
      'countryCode': ['', Validators.required],
      'businessName': ['', Validators.required],
      'jhaihoURL': ['', Validators.required],
      'address': ['', Validators.required],
      'customAddress': ['', Validators.required],
      'addressLocality': ['', Validators.required],
      'latitude': ['', Validators.required],
      'longitude': ['', Validators.required]
});
  }

  saveUser() {
    if (this.userForm.dirty && this.userForm.valid) {

        this.service.addsubadmin(this.userForm.value).subscribe(
            data => {
                console.log(data)
                 alert('User added Successfully');
                // this._router.navigate(['/subadmin']);
            },
            error => {
               console.log('error')
            });
       
    }

  }
  ngOnInit() {
  }

}
